.task1__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap; }

.task1__item {
  margin-bottom: 10px;
  margin-right: 10px;
  width: calc((100% - 30px)/4);
  max-width: 100%; }
  .task1__item:nth-child(4n) {
    margin-right: 0; }

@media (max-width: 1050px) {
  .task1__item {
    width: calc((100% - 10px)/2); }
    .task1__item:nth-child(2n) {
      margin-right: 0; } }

@media (max-width: 650px) {
