.task9__button {
  position: relative;
  display: block;
  margin-bottom: 10px;
  padding: 15px;
  padding-right: 35px;
  width: 270px;
  max-width: 100%;
  border-radius: 7px;
  border: 1px solid #ffffff;
  background-color: #F5F5F5; }
  .task9__button svg {
    position: absolute;
    top: 50%;
    right: 20px;
    transform: translateY(-50%) rotate(90deg);
    transition: all 0.5s ease-in; }
  .task9__button--active svg {
    transform: translateY(-50%) rotate(-90deg);
    transition: all 0.5s ease-in; }

.task9__list {
  list-style: none;
  margin: 0;
  padding: 0;
  max-height: 0;
  opacity: 0;
  transition: all 0.5s ease-in; }
  .task9__list--active {
    margin-bottom: 10px;
    opacity: 1;
    transition: all 0.5s ease-in; }

.task9__item {
  display: block;
  margin-bottom: 10px;
  padding: 15px;
  width: 270px;
  max-width: 100%;
  text-align: center;
  border-radius: 7px;
  border: 1px solid #ffffff;
  background-color: #F5F5F5; }

.card {
  padding: 10px;
  border-radius: 7px;
  border: 1px solid #ffffff;
  background-color: #F5F5F5; }
  .card :focus, .card:hover, .card:active {
