.task1__list {
  @include list();

  display: flex;
  flex-wrap: wrap;
}

.task1__item {
  margin-bottom: 10px;
  margin-right: 10px;
  width: calc((100% - 30px)/4);
  max-width: 100%;

  &:nth-child(4n) {
    margin-right: 0;
  }
}

@media(max-width:1050px) {
  .task1__item {
    width: calc((100% - 10px)/2);
  
    &:nth-child(2n) {
      margin-right: 0;
    }
  }
}

@media(max-width:650px) {
  .task1__list {
    display: block;
  }

  .task1__item {
    margin-right: 0;
    width: 100%;
  }
}

